#root {
  position: relative;
}

.none {
  display: none;
}
	
body {
  background: rgba(0, 102, 255, 0.38);
  font-family: sans-serif;
}

h3 {
  display: block;
  width: 160px;
  margin: 10px 0 0 230px;
  font-size: 36px;
}

p {
  margin: 0 0 5px;
}
	
.article {
  background: #FFF;
  border: 1px solid rgba(0, 89, 181, 0.82);
  border-radius: 10px;
  width: 600px;
  margin: 0 0 5px 10px;
  box-shadow: 2px 2px 5px -1px rgb(0, 81, 202);
  padding: 3px 5px;
}

.news__author {
  color: #EC4D3C;
  font-size: 20px;
}

.news__count {
  margin: 20px 0 10px 15px;
  display: block;
}

.news__readmore {
  text-decoration: none;
  color: #007DDC;
}

.test-input {
  margin: 0 5px 5px 0;
}
	
.add {
  position: absolute;
  left: 650px;
  top: 80px;
  margin: 10px 0 0 0;
  width: 210px;
  border: 1px dashed rgba(0, 89, 181, 0.82);
  padding: 5px;
}
	
.add__author, .add__text, .add__btn, .add__checkrule {
  display: block;
  margin: 0 0 5px 0;
  padding: 5px;
  width: 94%;
  border: 1px solid rgba(0, 89, 181, 0.82);
}
	
.add__checkrule {
  border: none;
  font-size: 12px;
}
	
.add__btn {
  box-sizing: content-box;
  color: #FFF;
  text-transform: uppercase;
  background: #007DDC;
}
	
.add__btn:disabled {
  background: #CCC;
  color: #999;
}









